.products {
  .wrap {
    background-color: $black;
    .title {
      overflow: hidden;
      text-align: center;
      font-size: 30px;
      &:before,
      &:after {
        content: "";
        display: inline-block;
        vertical-align: middle;
        width: 100%;
        height: 2px;
        background-color: #3c4047;
        position: relative;
      }
      &:before {
        margin-left: -100%;
        left: -50px;
      }
      &:after {
        margin-right: -100%;
        right: -50px;
      }
      h2 {
        display: inline-block;
        padding-top: 85px;
        padding-bottom: 40px;
        font-family: "Lato", sans-serif;
        font-weight: 800;
        font-size: 24px;
        color: white;
        text-align: center;
      }
    }
    .owl-carousel {
      display: none;
    }
    .list-items {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
    .product-item {
      display: flex;
      flex-direction: column;
      position: relative;
      width: 270px;
      border-radius: 5px;
      margin-bottom: 30px;
      background-color: white;
      z-index: 1;
      .buttons-block {
        display: none;
        position: absolute;
        z-index: 2;
        top: 34%;
        left: 13%;
        button {
          border: none;
          border-radius: 50%;
          padding: 32px;
          background-color: white;
          outline: none;
          i {
            font-size: 26px;
          }
        }
        .plus-button {
          color: #23c46e;
          margin-right: 24px;
        }
        .heart-button {
          color: #e73c68;
        }
      }
      .img-block {
        width: 100%;
        height: 260px;
        img {
          width: 100%;
          height: 265px;
          object-fit: cover;
        }
      }
      .text-block {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: flex-end;
        min-height: 77px;
        background-color: white;
        text-align: center;
        padding-left: 50px;
        padding-right: 50px;
        padding: 20px 10px 10px;
        color: $black;
        .text,
        .price {
          font-family: "Lato", sans-serif;
          font-weight: 400;
          font-size: 16px;
        }
        .text {
          padding-bottom: 5px;
          padding-left: 40px;
          padding-right: 40px;
        }
        .price {
          font-size: 14px;
          padding-top: 10px;
          padding-bottom: 10px;
        }
      }
      .price-block {
        display: flex;
        align-items: flex-end;
        border: 1px solid #e5e5e5;
        border-radius: 25px;
        padding: 8px 22px;
        position: relative;
        .price {
          color: $green;
          padding: 0;
          padding-right: 15px;
        }
        .line {
          position: absolute;
          width: 1px;
          height: 100%;
          top: 0;
          left: 44%;
          background: #e5e5e5;
        }
        button {
          border: none;
          background-color: transparent;
          text-transform: uppercase;
          color: black;
          padding-left: 15px;
        }
      }
    }
    .hover:hover::after {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: transparent;
      background-image: linear-gradient(to top, #1cc167, #2ed3a2);
      opacity: 0.9;
    }
    .hover:hover .buttons-block {
      display: block;
    }
    .product-item:nth-child(1) {
      .text-block {
        padding-left: 10px;
        padding-right: 10px;
        .text {
          padding-left: 0px;
          padding-right: 0px;
        }
      }
    }
    .item-buy {
      a {
        text-align: center;
        span {
          color: #262a32;
        }
        &:hover {
          span {
            color: $green;
          }
        }
      }
      .text-block {
        .price-block {
          margin-top: 5px;
        }
      }
      .text-block > a {
        padding-left: 40px;
        padding-right: 40px;
        .text {
          padding-left: 0px;
          padding-right: 0px;
        }
      }
    }
    .item-chat {
      background-image: url("../imgs/message.jpg");
      background-repeat: no-repeat;
      object-fit: cover;
      background-position: 85% 40%;
      display: flex;
      justify-content: flex-end;
      .text-block {
        position: relative;
        background: transparent;
        align-items: flex-end;
        justify-content: flex-start;
        text-align: left;
        z-index: 2;
        bottom: 0;
        color: white;
        padding-bottom: 40px;
        .time {
          position: relative;
          text-transform: uppercase;
          text-align: left;
          padding-left: 70px;
          padding-right: 40px;
          padding-top: 20px;
          &::before {
            content: "";
            position: absolute;
            left: 42px;
            bottom: 2px;
            width: 28px;
            height: 28px;
            background-image: url("../imgs/icons/chat.png");
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
          }
        }
      }
      &::after {
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background: transparent;
        background-image: linear-gradient(to top, #675b99, #dc5987);
        opacity: 0.8;
      }
    }
    .load-items-block {
      display: flex;
      justify-content: center;
      .load-items-button {
        width: 266px;
        margin-top: 18px;
        margin-bottom: 56px;
        text-transform: uppercase;
        background: transparent;
        border: 1px solid white;
        border-radius: 25px;
        padding: 12px 10px;
        font-family: "Lato", sans-serif;
        font-weight: 400;
        font-size: 14px;
        color: white;
        span {
          padding-right: 23px;
        }
      }
    }
  }
}
