@media only screen and (max-width: 1279px) {
  .wrap {
    padding: 0 15px;
  }
  header {
    .logo-block {
      width: 8%;
      p {
        display: none;
      }
    }
    .navbar-block {
      width: 67%;
      margin-right: 3%;
    }
  }
  .banners-block {
    padding-top: 0;
    .owl-carousel {
      .item {
        h1 {
          font-size: 40px;
          width: 90%;
        }
        p {
          width: 80%;
          padding-bottom: 60px;
        }
      }
      .first-item {
        padding-left: 10px;
      }
      .third-item {
        h1,
        p {
          padding-right: 10px;
        }
      }
    }
    .banner-sale {
      width: 24%;
      margin-left: 2%;
      .banner-summer {
        width: 100%;
        span {
          font-size: 80px;
        }
      }
      .text-fb {
        p:nth-child(1) {
          padding-top: 20px;
          padding-bottom: 10px;
        }
        p:nth-child(2) {
          padding-bottom: 20px;
        }
      }
    }
  }
  .products {
    .wrap {
      .list-items {
        display: none;
      }
      .load-items-block {
        display: none;
      }
      .owl-carousel {
        display: block;
        .product-item {
          width: 100%;
          .buttons-block {
            top: calc(50% - 35px);
            left: calc(50% - 78px);
            .plus-button,
            .heart-button {
              padding: 20px;
            }
          }
          .text-block {
            .text {
              width: 100%;
            }
          }
          .price-block {
            padding: 8px 2px;
          }
        }
        .item-buy .text-block > a {
          padding-left: 0px;
          padding-right: 0px;
        }
        .owl-dots {
          margin-top: 0;
          padding-bottom: 30px;
          .owl-dot {
            outline: none;
          }
        }
      }
    }
  }
  .featured-products {
    .wrap {
      .items-carousel {
        .owl-carousel {
          .item {
            .img-block {
              height: 150px;
              a {
                img {
                  height: 150px;
                }
              }
            }
            .text-block {
              a {
                font-size: 13px;
              }
            }
          }
        }
      }
    }
  }
  footer {
    .contact-info {
      .categories-block {
        margin-right: 0;
      }
    }
  }
}

@media only screen and (max-width: 767px) {
  .container {
    width: 100%;
  }
  header {
    .logo-block {
      width: 100%;
      height: 60px;
      justify-content: center;
      a {
        img {
          width: 40px;
          height: 40px;
        }
      }
    }
    .navbar-block {
      width: 0;
      margin-right: 0;
      .hamburger-button {
        display: block;
      }
      .navbar {
        display: none;
        overflow: auto;
        height: 80vh;
        width: 100%;
        align-items: flex-start;
        position: absolute;
        top: 60px;
        left: 0;
        flex-direction: column;
        background-color: $black;
        z-index: 1000;
        .dropdown-block {
          width: 100%;
          &:hover .dropbtn {
            border-bottom: none;
            margin-top: 0;
            padding-bottom: 0;
          }
          .dropbtn {
            padding-left: 15px;
            color: white;
            padding-bottom: 20px;
            width: 98%;
            text-align: left;
            background-image: none;
            position: relative;
            &:hover {
              padding-bottom: 0;
              color: $green;
              .fa-caret-down {
                display: none;
              }
              .fa-caret-up {
                display: inline-block;
              }
            }
            .fa-caret-down,
            .fa-caret-up {
              position: absolute;
              top: 15px;
              right: 5px;
            }
            .fa-caret-down {
              display: inline-block;
            }
            .fa-caret-up {
              display: none;
            }
          }
          .dropdown-content {
            padding-top: 10px;
            padding-bottom: 10px;
            position: relative;
            top: 0;
            .wrap {
              flex-direction: column;
              .categories-block {
                width: 100%;
                .dropbtn-categories {
                  padding-left: 10px;
                  padding-right: 20px;
                  outline: none;
                  position: relative;
                  span {
                    text-transform: initial;
                    font-weight: 400;
                  }
                  .fa-caret-down,
                  .fa-caret-up {
                    position: absolute;
                    top: 0px;
                    right: 0px;
                    color: white;
                    font-size: 14px;
                  }
                  .fa-caret-down {
                    display: inline-block;
                  }
                  .fa-caret-up {
                    display: none;
                  }
                }
                .dropdown-categories-content {
                  display: none;
                  flex-wrap: nowrap;
                  height: auto;
                  a {
                    padding-bottom: 0;
                    padding-left: 20px;
                  }
                }
                &:hover {
                  .dropbtn-categories {
                    .fa-caret-down {
                      display: none;
                    }
                    .fa-caret-up {
                      display: inline-block;
                    }
                  }
                  .dropdown-categories-content {
                    display: flex;
                  }
                }
              }
              .sale-block {
                width: 100%;
                .dropbtn-sale {
                  padding-right: 20px;
                  padding-left: 10px;
                  margin-top: 10px;
                  outline: none;
                  position: relative;
                  span {
                    text-transform: initial;
                    font-weight: 400;
                    padding-bottom: 10px;
                  }
                  .fa-caret-down,
                  .fa-caret-up {
                    font-size: 14px;
                    position: absolute;
                    top: 0px;
                    right: 0px;
                    color: white;
                  }
                  .fa-caret-down {
                    display: inline-block;
                  }
                  .fa-caret-up {
                    display: none;
                  }
                }
                .dropdown-sale-content {
                  display: none;
                  flex-wrap: nowrap;
                  height: auto;
                  a {
                    padding-bottom: 0;
                    padding-left: 20px;
                  }
                }
                &:hover {
                  .dropbtn-sale {
                    .fa-caret-down {
                      display: none;
                    }
                    .fa-caret-up {
                      display: inline-block;
                    }
                  }
                  .dropdown-sale-content {
                    display: flex;
                  }
                }
              }
              .nav-img {
                display: none;
              }
            }
          }
        }
        a {
          display: inline-block;
          text-align: left;
          padding-bottom: 28px;
          padding-left: 15px;
          color: white;
        }
        .nav-img-outer {
          display: flex;
          justify-content: center;
          width: 100%;
          margin-top: 200px;
          margin-bottom: 20px;
          img {
            width: 270px;
            height: 276px;
            object-fit: cover;
            border-radius: 2%;
          }
        }
        .language-form,
        .currency-form {
          display: none;
        }
      }
      .navbar > a {
        display: inline-block;
        width: calc(98% - 15px);
        text-align: left;
        position: relative;
        .fa-caret-down,
        .fa-caret-up {
          position: absolute;
          top: 0px;
          right: 5px;
        }
        .fa-caret-down {
          display: inline-block;
        }
        .fa-caret-up {
          display: none;
        }
        &:hover {
          .fa-caret-down {
            display: none;
          }
          .fa-caret-up {
            display: inline-block;
          }
        }
      }
    }
    .navbar-block:hover {
      span {
        width: 0;
        &::after,
        &::before {
          transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
        }
        &::after {
          bottom: 0;
          transform: rotate(45deg);
        }
        &::before {
          top: 0;
          transform: rotate(-45deg);
        }
      }
      .navbar {
        display: flex;
      }
    }
  }
  .icons-block {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 80%;
    background-color: #262a32;
    padding: 17px 12% 10px;
    justify-content: space-around;
    p {
      .wish-circle,
      .cart-circle {
        background-color: #262a32;
      }
    }
    .login-block {
      .modal-content {
        width: 80%;
        padding: 10px;
        h3 {
          padding-bottom: 10px;
        }
        input {
          width: 100%;
        }
      }
    }
  }
  .banners-block {
    flex-direction: column;
    margin-top: 20px;
    .owl-carousel {
      width: 100%;
      .owl-dots {
        bottom: 15px;
      }
      .item {
        padding-left: 15px;
        padding-right: 15px;
        h1 {
          width: 100%;
          font-size: 25px;
          padding-top: 25px;
          padding-bottom: 50px;
        }
        p {
          width: 100%;
        }
        a {
          margin-bottom: 65px;
          margin-left: auto;
          margin-right: auto;
        }
      }
      .first-item {
        background-position: right top;
      }
      .third-item {
        p {
          padding-right: 0px;
        }
        a {
          margin-left: auto;
          margin-right: auto;
        }
      }
    }
    .banner-sale {
      display: none;
    }
  }
  .products {
    .wrap {
      .title {
        h2 {
          font-size: 18px;
          padding-top: 30px;
          padding-bottom: 20px;
        }
      }
      .title:before {
        left: -20px;
      }
      .title:after {
        right: -20px;
      }
      .list-items {
        display: none;
      }
      .load-items-block {
        display: none;
      }
      .owl-carousel {
        .item {
          width: 100%;
        }
      }
    }
  }
  .static-banner {
    .wrap {
      .banner-block {
        min-height: auto;
        h2 {
          font-size: 30px;
          padding-top: 30px;
          padding-bottom: 30px;
        }
        p {
          max-width: 100%;
          padding-bottom: 60px;
        }
      }
    }
  }
  .featured-products {
    display: none;
  }
  .services {
    .wrap {
      .items-block {
        flex-direction: column;
        .item {
          width: 100%;
          padding-top: 27px;
          padding-bottom: 36px;
          align-items: flex-start;
          .img-block {
            display: flex;
            width: 50%;
            justify-content: center;
          }
          .text-block {
            width: 50%;
            padding: 0;
          }
        }
        .item:nth-child(2) {
          flex-direction: row-reverse;
        }
      }
    }
  }
  footer {
    .contact-info {
      flex-direction: column;
      padding-top: 25px;
      padding-bottom: 55px;
      .contacts {
        width: 100%;
        margin-right: 0;
        .copyright-block {
          padding-bottom: 25px;
          h3 {
            padding-bottom: 20px;
          }
        }
        .contact-block {
          h3 {
            padding-bottom: 20px;
            position: relative;
            .fa-caret-down,
            .fa-caret-up {
              position: absolute;
              top: 0px;
              right: 0px;
            }
            .fa-caret-down {
              display: inline-block;
            }
            .fa-caret-up {
              display: none;
            }
          }
          .contact-details {
            display: none;
            width: 70%;
            padding-bottom: 25px;
            .email {
              padding-top: 23px;
              padding-bottom: 23px;
            }
          }
          &:hover {
            h3 {
              .fa-caret-down {
                display: none;
              }
              .fa-caret-up {
                display: inline-block;
              }
            }
            .contact-details {
              display: block;
            }
          }
        }
      }
      .categories-block {
        width: 100%;
        .dropbtn-categories {
          position: relative;
          outline: none;
          width: 100%;
          text-align: left;
          .fa-caret-down,
          .fa-caret-up {
            position: absolute;
            top: 0px;
            right: 0px;
          }
          .fa-caret-down {
            display: inline-block;
          }
          .fa-caret-up {
            display: none;
          }
        }
        .dropdown-categories-content {
          display: none;
        }
        &:hover {
          .fa-caret-down {
            display: none;
          }
          .fa-caret-up {
            display: inline-block;
          }
          .dropdown-categories-content {
            display: flex;
          }
        }
      }
      .about-block {
        width: 100%;
        .dropbtn-about {
          position: relative;
          outline: none;
          width: 100%;
          text-align: left;
          .fa-caret-down,
          .fa-caret-up {
            position: absolute;
            top: 0px;
            right: 0px;
          }
          .fa-caret-down {
            display: inline-block;
          }
          .fa-caret-up {
            display: none;
          }
        }
        .dropdown-about-content {
          display: none;
        }
        &:hover {
          .fa-caret-down {
            display: none;
          }
          .fa-caret-up {
            display: inline-block;
          }
          .dropdown-about-content {
            display: flex;
            flex-direction: column;
          }
        }
      }
      .social-block {
        justify-content: center;
        width: 100%;
        padding-bottom: 65px;
        a:nth-child(1) {
          padding-left: 0;
        }
      }
    }
  }
}
