@import "normalize";
@import "fonts";

$black: #262a32;
$gray: #565656;
$green: #84bc22;

* {
  margin: 0px;
  padding: 0px;
}

.container {
  max-width: 1280px;
  min-width: 320px;
  margin: 0 auto;
}

.wrap {
  max-width: 1280px;
  padding: 0 55px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

.hidden {
  display: none;
}

body {
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
  a:hover,
  a:active {
    color: $green;
  }
}

@import "header";

@import "main-banner";

@import "products";

@import "static-banner";

@import "featured-products";

@import "services";

@import "footer";

@import "media";
